body.page-flow {
	.datepicker-container-block {
		display: inline-block; }

	.flow-container {
		h3.substep-title {
			font-weight: normal;
			font-size: 26px;
			color: $greyish-brown;
			margin: 20px 0 30px; }

		p.substep-explanation {
			font-weight: normal;
			font-size: 14px;
			line-height: 1.3;
			color: $warm-grey;
			margin: 30px 0 0; }

		.step {
			display: none;

			&.step--active {
				display: block; }

			.sub_step {
				position: relative;
				display: none;
				text-align: center;

				&.sub_step--active {
					display: block; } }

			label {
				position: relative;
				cursor: pointer;
				padding: 15px;
				background: #f9f9f9;
				border-radius: 4px;
				display: inline-block;
				margin-right: 10px;
				font-weight: 600;
				-webkit-user-select: none;
				-moz-user-select: none;
				-ms-user-select: none;
				user-select: none;

				.icon {
					margin-bottom: 10px; }

				&:last-of-type {
					margin-right: 0; }

				&.flow_label_noclick {
					.icon {
						margin: 0; } }

				&:hover {
					background: #e5e5e5; }

				input[type="radio"] {
					display: none; } } } } }

.flow-step-back {
	margin: 20px 0;

	a {
		opacity: 0.5;
		cursor: not-allowed;
		padding: 10px;
		display: inline-block;
		color: #aaa; }

	&.clickable {
		a {
			opacity: 1;
			cursor: pointer; } } }

.flow-progress {
	text-align: center;
	background: $pale-grey;
	border-bottom: 1px solid $pale-grey-border;
	font-size: 0;

	.hamburger {
		display: none; }

	.flow-step {
		background: $white;
		height: 66px;
		padding: 25px 50px;
		display: inline-block;
		text-align: center;
		border-right: 1px solid $pale-grey-border;

		&:first-child {
			border-left: 1px solid $pale-grey-border; }

		.flow-step-check {
			display: none; }

		.flow-step-progress {
			margin-right: 12px;
			width: 26px;
			height: 2px;
			display: inline-block;
			vertical-align: middle;
			background: $lighter-grey;
			transition: width 300ms;

			.flow-step-progress-bar {
				background: $main-green;
				height: 2px;
				display: block;
				transition: width 300ms; } }

		.flow-step-title {
			display: inline-block;
			vertical-align: middle;
			font-weight: 600;
			font-size: 14px;
			letter-spacing: 1px;
			color: $warm-grey; }

		&.flow-step--inactive {
			background: transparent;

			.flow-step-progress {
				background: #aaa;
				width: 2px;

				.flow-step-progress-bar {
					display: none; } }

			.flow-step-title {
				color: $greyish; } }

		&.flow-step--completed {
			background: #fff;
			background: linear-gradient(to right, transparent 33%, rgba($main-green, .4) 50%, #fff 66%);
			background-size: 300% 100%;
			animation-name: flow_step_success;
			animation-duration: 500ms;
			animation-iteration-count: 1;

			.flow-step-title {
				color: $warm-grey; }

			.flow-step-progress {
				display: none; }

			.flow-step-check {
				margin-right: 12px;
				display: inline-block;
				display: inline-block;
				position: relative; } } } }

@-webkit-keyframes flow_step_success {
	0% {
		background-position: right bottom; }

	100% {
		background-position: left bottom; } }

@keyframes flow_step_success {
	0% {
		background-position: right bottom; }

	100% {
		background-position: left bottom; } }

@media all and (max-width: 991px) {
	body.page-flow {
		.datepicker-container-block {
			display: block; }

		.flow-progress {
			border-bottom: none !important;
			position: relative;

			.hamburger {
				display: block;
				position: absolute;
				right: 15px;
				top: 14px; }

			.flow-step {
				width: 100%;
				padding: 25px 15px;
				border-right: none !important;
				border-left: none !important;
				border-bottom: 1px solid $pale-grey-border;
				text-align: left;
				-webkit-transition: height 300ms, padding 300ms, border 300ms;
				transition: height 300ms, padding 300ms, border 300ms;

				&.flow-step--inactive .flow-step-progress {
					display: none; } }

			&.flow-progress--closed {
				.flow-step {
					&.flow-step--inactive {
						height: 0;
						overflow: hidden;
						padding-top: 0;
						padding-bottom: 0;
						border: none; } } } }

		.flow-container {
			.step {
				.sub_step {
					.sub_step_answers {
						-webkit-overflow-scrolling: touch;
						overflow-x: scroll;
						overflow-y: hidden;
						white-space: nowrap;

						label {
							min-width: 40%; } } } } } } }
